<template>
  <div class="menu-wrapper">
    <draggable
      v-model="routes"
      group="people"
      @start="drag = true"
      @end="drag = false"
    >
      <template v-for="item in routes" v-if="!item.hidden && item.children">
        <router-link
          v-if="
            item.children.length === 1 &&
            !item.children[0].children &&
            !item.alwaysShow
          "
          :to="item.path + '/' + item.children[0].path"
          :key="item.children[0].name"
        >
          <el-menu-item
            :index="item.path + '/' + item.children[0].path"
            :class="{ 'submenu-title-noDropdown': !isNest }"
          >
            <span v-if="item.children[0].meta && item.children[0].meta.title">{{
              item.children[0].meta.title
            }}</span>
          </el-menu-item>
        </router-link>

        <el-submenu v-else :index="item.name || item.path" :key="item.name">
          <template slot="title">
            <span v-if="item.meta && item.meta.title">{{
              item.meta.title
            }}</span>
          </template>

          <template v-for="child in item.children" v-if="!child.hidden">
            <sidebar-item
              :is-nest="true"
              class="nest-menu"
              v-if="child.children && child.children.length > 0"
              :routes="[child]"
              :key="child.path"
            ></sidebar-item>

            <router-link
              v-else
              :to="item.path + '/' + child.path"
              :key="child.name"
            >
              <el-menu-item :index="item.path + '/' + child.path">
                <span v-if="child.meta && child.meta.title">{{
                  child.meta.title
                }}</span>
              </el-menu-item>
            </router-link>
          </template>
        </el-submenu>
      </template>
    </draggable>
  </div>
</template>

<script>
import draggable from "vuedraggable";
export default {
  name: "sidebarItem",
  props: {
    showRoutes: {
      type: Array,
    },
    isNest: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      routes: [],
    };
  },
  created() {
    this.routes = this.showRoutes;
  },
  components: {
    draggable,
  },
  methods: {
    unique(arr) {
      var newArr = [arr[0]];
      for (var i = 1; i < arr.length; i++) {
        if (newArr.indexOf(arr[i]) === -1) {
          newArr.push(arr[i]);
        }
      }
      return newArr;
    },
  },
  watch: {
    routes: function (newVal, oldVal) {
      var localRouter = JSON.parse(localStorage.getItem("router"));
      var localRouterPath = localRouter.map(item=>item.path);
      var showRoutePath = this.routes.map(item=>item.path);

      var newLocalRouter = [];
      // console.log("localRouter", localRouter);
      // console.log("newVal", newVal);
      console.log(this.routes);
      console.log("localRouterPath===",localRouterPath)
      console.log("showRoutePath===",showRoutePath)
      var _this = this;
      if (oldVal.length > 0) {
        localRouter.sort((star, next) => {
          const d = _this.unique([...showRoutePath,...localRouterPath])
          console.log(d)
          return d.indexOf(star.path) - d.indexOf(next.path);
          // return sortList.indexOf(star.code) - sortList.indexOf(next.code);
        });
        window.localStorage.setItem('router', JSON.stringify(localRouter))
      }
      console.log('119=localRouter===========',localRouter);
    },
  },
};
</script>
