import login from '@/views/login' //Layout 是架构组件，不在后台返回，在文件里单独引入
import errorPage from '@/views/errorPage' //Layout 是架构组件，不在后台返回，在文件里单独引入
const staticRouter = [{
  "path": "/",
  "component":"login",
  "redirect": "login",
  "children": [{
    "path": "login",
    "component": "login/index",
    "meta": {
      "title": "登录",
      "icon": "login"
    }
  }],
  "hidden": true
}, {
  "path": "*",
  "component": "errorPage",
  "redirect": "/404",
  "children": [{
    "path": "404",
    "name": "404",
    "component": "errorPage/index",
    "meta": {
      "title": "404",
      "icon": "form"
    }
  }],
  "hidden": true
}]

export default staticRouter